import React from 'react';

import { Info } from './Info';

import styles from './card.module.scss';

const format = (a, delim, end) => {
  let res = '';

  for(let i=0;i<a.length-1;i++) {
    const curDelim = i === a.length-2 ? '' : delim;
    res += `${a[i]}${curDelim} `;
  }

  res += `${end}${a[a.length - 1]}`;
  return res;
}

export const Card = ({ location, address, days, time }) => {

  const formatDays = format(days, ',', ' i ');

  return(
    <div className={styles.card}>
      <h3 className={styles.heading}>{location}</h3>

      <div className={styles.infoContainer}>
        <Info icon={'map'} info={address} />
        <Info icon={'calendar'} info={formatDays}/>
        <Info icon={'stopwatch'} info={time} />
      </div>
    </div>
  );
}