import { useLocation, useHistory } from 'react-router-dom';
import { useInView } from 'react-intersection-observer';
import { useEffect } from 'react';

export const useDynamicLocation = () => {
  const location = useLocation();
  const history = useHistory();
  const { ref, inView, entry } = useInView({ threshold: 1.0 });

  useEffect(() => {
  
    if(inView) {
      const { id } = entry.target;
  
      const newLocation = { ...location, hash: `#${id}` };
  
      // console.log('location', location, 'newLocation', newLocation, 'bool', location.hash !== `#${id}`);
  
      if(location.hash !== `#${id}`) history.replace(newLocation);
      // console.log(`in view ${id}`);
    }
  });

  // useCallback(() => {
  //   const { id } = entry.target;
  //   const newLocation = { ...location, hash: `#${id}` };

  //   console.log(`Called for ${id}`);

  //   if(inView) history.replace(newLocation);
  // }, [inView]);

  return { ref };
}