import React from 'react';

import styles from './sectionHeading.module.scss';

export const SectionHeading = ({ children, white, secondary }) => {
  const concat = classes => {
    let res = '';

    for(let c of classes) res += `${c} `;
    return res;
  }
  
  const classes = [styles.heading];

  if(white) classes.push(styles.white);
  if(secondary) classes.push(styles.secondary);
  
  //const classes = white ? `${styles.heading} ${styles.white}` : styles.heading;
  return (
    <h3 className={concat(classes)}>{children}</h3>
  );
}