import React from 'react';

import ReactCarousel, { Dots } from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

import data from './data.json';

import styles from './carousel.module.scss';

export const Carousel = ({ value, onChange }) => {
  const r = require.context('./', false, /\.(png|jpe?g)$/);

  let images = data.map(({ image: url }, i) => {
    return <img className={styles.image} key={i} src={r(url)} alt="" /> ; 
  });
  

  return (
    <div className={styles.carouselContainer}>
      <ReactCarousel fastSwipe arrows clickToChange value={value} onChange={onChange}>
        { images }
      </ReactCarousel>

      <Dots value={value} onChange={onChange} number={images.length}/>
    </div>
  );
}