import React from 'react';
import { NavHashLink as NavLink } from 'react-router-hash-link';

import { useLocation } from 'react-router-dom';

import styles from './link.module.scss';

export const Link = ({ children, hash }) => {
  const location = useLocation();
  const classes = location.hash === `#${hash}` ? `${styles.link} ${styles.active}` : styles.link;

  const scroll = e => { 
    const offset = -30;
    const y = e.getBoundingClientRect().top + window.pageYOffset + offset;

    window.scrollTo({ top: y, behavior: 'smooth' });

    // e.scrollIntoView({ behavior: 'smooth', block: 'start', inline: 'start' });
   // window.scrollBy(0, -30);
  }

  return (
    <NavLink className={classes} activeClassName="" scroll={scroll} to={`/#${hash}`}>{children}</NavLink>
  );
}