import React, { useState } from 'react';

import { SectionHeading } from '../SectionHeading';
import { Carousel } from './Carousel';
import { Place } from './Place';

import { useDynamicLocation } from '../hooks/useDynamicLocation';

import data from './data.json';
import styles from './achievements.module.scss';

export const Achievements = () => {
  const [ index, setIndex ] = useState(0);
  const { ref } = useDynamicLocation();

  const onChange = value => {
    setIndex(value);
  }

  const { title, date, people, places } = data[index];

  const displayPlaces = places.map((place, i) => <Place place={place} name={people[i]}/>);

  return (
    <section className={styles.achievements} id="achievements" ref={ref}>
      <SectionHeading>Naša Postignuća</SectionHeading>
      <Carousel value={index} onChange={onChange}/>

      <div className={styles.description}>
        <div className={styles.header}>
          <h3 className={styles.title}>{title}</h3>
          <p className={styles.date}>{date}</p>
        </div>

        <div className={styles.places}>
          {displayPlaces}
        </div>
      </div>
    </section>
  );
}