import React from 'react';

import { SectionHeading } from '../SectionHeading';
import { Card } from './Card';

import { useDynamicLocation } from '../hooks/useDynamicLocation';

import styles from './locations.module.scss';
import data from './data.json';

export const Locations = () => {
  const { ref } = useDynamicLocation();

  const displayData = data.map((props) => <Card {...props}/>);
  
  return (
    <section className={styles.locations} id="locations" ref={ref}>
      <SectionHeading white>Lokacije</SectionHeading>
      <div className={styles.cardContainer}>
        {displayData}
      </div>
    </section>
  );
}