import React, { useState } from 'react';

import { Contact } from './Contact';
import { Link } from './Link';

import styles from './nav.module.scss';
import icons from './icons.svg';

export const Nav = () => {
  const  [collapsed, setCollapsed]  = useState(true);

  const classes = collapsed ? `${styles.nav} ${styles.collapsed}` : styles.nav;
  const buttonIcon = collapsed ? 'menu' : 'cross';

  const toggleClick = () => setCollapsed(collapsed => !collapsed);

  return (
    <div>
      <button onClick={toggleClick} className={styles.toggle}>
        <svg 
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        className={styles.toggleIcon}>
          <use xlinkHref={`${icons}#icon-${buttonIcon}`}></use>
        </svg>
      </button>

      <nav className={classes}>
        <Contact />
        <ul className={styles.linkContainer}>
          <Link hash="about">O nama</Link>
          <Link hash="locations">Lokacije</Link>
          <Link hash="achievements">Postignuća</Link>
        </ul>
      </nav>
      
    </div>
  );
};